import { Loader } from '@googlemaps/js-api-loader';
import { MarkerClusterer, SuperClusterAlgorithm } from '@googlemaps/markerclusterer';

interface MapOptions {
    apiKey: string; 
    center?: google.maps.LatLngLiteral;
    zoom?: number;
    clusterable?: boolean | false;
    idMaps: string; 
}

interface MarkerManaged 
{
    Id:string;
    marker?: google.maps.marker.AdvancedMarkerElement;
    draggendCallback?: (eventPosition:EventPositionMarker) => void;
    clickMarkerCallback?: (eventClickMarker:EventClickMarker) => void;
    address?:string;
    geocodable?: boolean;
    callable?: boolean;
}

//de cara al usuario, lo que me va a pasar para crear o actualizar un marcador
interface MarkerInfo {
    id:string;
    draggable: boolean|false; //si se va a poder arrastrar el marcador
    geocodable: boolean|false; //si va a resolver la direccin cuando se fije.
    position: google.maps.LatLngLiteral;
    address?:string| '';
    title: string;
    icon: string;    
    glyph?: string | '';
    scale?: number | 1.0;
    background?: string | '#E53935';
    glyphColor?: string | '#FFFFFF';
    callable?: boolean| false;
    toCenter?: boolean|false;
    draggendCallback?: (eventPosition:EventPositionMarker) => void;
    clickMarkerCallback?: (eventClickMarker:EventClickMarker) => void;
}

interface MakerCoordenate
{
    id:string;
    position: google.maps.LatLngLiteral;
    address?:string|undefined;
}

interface EventPositionMarker{
    id:string,
    lat:number,
    lng:number
}

interface EventClickMarker{
    id:string
}

interface PolygonManaged {
    id: string;
    polygon: google.maps.Polygon;
}

interface PolygonInfo {
    id: string;
    paths: google.maps.LatLngLiteral[];
    strokeColor: string;
    strokeOpacity: string;
    strokeWeight: string;
    fillColor: string;
    fillOpacity: string;
    centro: PolygonCenter|null;
    visible: boolean| false;
}

interface PolygonCenter
{
    id: string;
    position: google.maps.LatLngLiteral;
    label:string;
    icon:string;
}

interface ContentWindowInfo
{
    id:string;
    content:string;
}

//Manejador avanzador de mapas

class MapManager 
{
    static DEFAULT_CENTER = { lat: -34.6037, lng: -58.3816 };
    static DEFAULT_ZOOM = 13;
        
    public version: string ='MapManager.0.0.5';

    private markerClusterer: MarkerClusterer | null = null;
    private clusterable: boolean = false;
    private marks = new Map<string, MarkerManaged>();
    private polygons = new Map<string, PolygonManaged>();
    public map: google.maps.Map | null = null;    

    private initPromise: Promise<void>;

    constructor(element: string | HTMLElement, optionsInfo: MapOptions) 
    {
        if (!optionsInfo.apiKey) 
        {
            throw new Error('API Key is required');
        }
        this.initPromise = this.init(element, optionsInfo);
    }

    private async init(element: string | HTMLElement, optionsInfo: MapOptions): Promise<void> 
    {
        try 
        {
            this.clusterable = optionsInfo.clusterable || false;

            let loader: Loader | null = null;

            loader = new Loader({ apiKey: optionsInfo.apiKey, version: 'weekly', libraries: ['places', 'geometry'] });

            //await loader.load();
            await loader.load();

             await google.maps.importLibrary("marker");

            const mapElement = typeof element === 'string' ? document.getElementById(element) : element;

            if (!mapElement) {
                throw new Error('Map container not found');
            }

            this.map = new google.maps.Map(mapElement, 
            {
                center: optionsInfo.center || MapManager.DEFAULT_CENTER,
                zoom: optionsInfo.zoom || MapManager.DEFAULT_ZOOM,
                mapId: optionsInfo.idMaps ? optionsInfo.idMaps : 'map'
            });
          
            if(this.clusterable)
            {
                let map=this.map;
                this.markerClusterer = new MarkerClusterer({
                    map,
                    algorithm: new SuperClusterAlgorithm({
                        radius: 100, // Radio de agrupacin en pxeles
                        maxZoom: 15  // Zoom mximo para agrupar                   
                    })
                });
            }

            console.log(this.version);
        } 
        catch (error) 
        {
            console.error('Error initializing map:', error);
            throw error;
        }
    }

    public async isReady(): Promise<void> 
    {
        return this.initPromise;
    }

    public getMarkerManaged(id: string): MarkerManaged | undefined
    {
        return this.marks.get(id);
    }

    public async addMarkerManaged( markerInfo: MarkerInfo): Promise<void>    
    {
         await this.isReady();

        /*
        const pin = new google.maps.marker.PinElement({
            glyph: markerInfo.glyph || '',
            scale: markerInfo.scale || 1.0,
            background: markerInfo.background || '#E53935',
            glyphColor: markerInfo.glyphColor || '#FFFFFF'
        });
        */

        const marker = new google.maps.marker.AdvancedMarkerElement({
            map: this.map,
            position: markerInfo.position,
            //content: pin.element,
            content: this.createCustomIcon(markerInfo.icon || 'default-icon.png' ),
            title: 'Arrastra para mover' ,
            gmpDraggable: markerInfo.draggable || false,
            gmpClickable: markerInfo.callable || false // Necesario para interactividad
        });

        let markerManaged:MarkerManaged={
            Id: markerInfo.id, 
            marker: marker, 
            address: markerInfo.address,
            geocodable: markerInfo.geocodable || false,
            draggendCallback: markerInfo.draggendCallback || undefined,
            clickMarkerCallback: markerInfo.clickMarkerCallback || undefined,            
        }

        this.marks.set(markerInfo.id, markerManaged);

        if(markerManaged.draggendCallback)
        {
            this.addDragListener(markerManaged.Id, markerManaged.draggendCallback);
        }

        if(markerManaged.clickMarkerCallback)
        {
            this.addMarkerClickListener(markerManaged.Id,markerManaged.clickMarkerCallback);
        }

        if(this.clusterable && this.markerClusterer)
        {
             this.markerClusterer.addMarker(marker);
        }
    }
    
    public async updateMarkerManaged( markerInfo: MarkerInfo): Promise<void>
    {
         await this.isReady();

        const mark = this.marks.get(markerInfo.id);
        if (mark)
        {
            const hasAddressChanged = mark.address !== markerInfo.address ;
            const resolvCoords = hasAddressChanged && markerInfo.geocodable;

            if(hasAddressChanged)
            {
                mark.address= markerInfo.address;
            }

            if(resolvCoords &&  mark.marker && mark.marker.position)
            {
                const geocoder = new google.maps.Geocoder();
                const results = await geocoder.geocode({ address: mark.address });
                if (results.results.length > 0) 
                {
                    const location = results.results[0].geometry.location;
                    const newPos = { lat: location.lat(), lng: location.lng() };
                    mark.marker.position = newPos;
                }
                //sino serian las coordenadas del centro del mapa
            }

            if(!hasAddressChanged &&  mark.marker && mark.marker.position)
            {
                const currentPos = mark.marker.position;
                const hasPositionChanged =( currentPos && ( currentPos.lat !== markerInfo.position.lat ||        
                                                      currentPos.lng !== markerInfo.position.lng) ) ||
                                                    (!currentPos && markerInfo.position);

                if(hasPositionChanged && mark && mark.marker)
                {
                    mark.marker.position = markerInfo.position;
                }
            }

            if(markerInfo.toCenter && this.map && mark.marker && mark.marker.position )
            {
                this.map?.setCenter(mark.marker.position);
            }
        }
    }

    public removeMarkerManaged(id: string): void 
    {
        const mark = this.marks.get(id);
        if (mark &&  mark.marker ) 
        {
            this.removeDragListener(id, () => {});  
            mark.marker.map = null;
            this.marks.delete(id);
        }
    }

    addDragListener(id: string, callback: (eventPosition:EventPositionMarker) => void): void 
    {
        const mark = this.marks.get(id);

        console.log(`---> ${id}`);

        if (mark && mark.marker)
        {   
            mark.draggendCallback = callback;  
            mark.marker.addListener('dragend', () => 
            {
                const pos = mark.marker?.position;

                if (mark.draggendCallback  && pos )//&& typeof mark.markerDropCallback==='function') 
                {
                    const lat = typeof pos.lat === 'function' ? pos.lat() : pos.lat;
                    const lng = typeof pos.lng === 'function' ? pos.lng() : pos.lng;
                    mark.draggendCallback({ id: mark.Id, lat:lat, lng:lng });
                }
            });
        }    
    }

    removeDragListener(id: string, callback: (pos: google.maps.LatLngLiteral) => void): void 
    {
        const mark = this.marks.get(id);
        if (mark)
        {
            mark?.marker?.removeEventListener('dragend',  () => {});
            mark.draggendCallback = undefined;
        }
    }

    addMarkerClickListener(id: string, callback: (eventClickMarker:EventClickMarker) => void): void 
    {
        const mark = this.marks.get(id);

        if (mark &&  mark.marker )
        {   
            mark.clickMarkerCallback = callback;  
            mark.marker.addListener('click', () => 
            {
                if (mark.clickMarkerCallback) 
                {
                    mark.clickMarkerCallback({ id: mark.Id })
                }
            });
        }
    }

    removeMarkerClickListener(id: string, callback: (pos: google.maps.LatLngLiteral) => void): void 
    {
        const mark = this.marks.get(id);
        if (mark)
        {
            mark?.marker?.removeEventListener('click',  () => {});
            mark.clickMarkerCallback = undefined;
        }
    }

    public async addPolygonManaged( polygonInfo:PolygonInfo ): Promise<void>    
    {
         await this.isReady();

        if (!this.map) {
            console.error('Map not initialized');
            return;
        }

        const polygonOptions: google.maps.PolygonOptions = {
            paths: polygonInfo.paths,
            strokeColor: polygonInfo.strokeColor || '#FF0000',
            strokeOpacity: parseFloat(polygonInfo.strokeOpacity) || 0.8,
            strokeWeight: parseInt(polygonInfo.strokeWeight) || 2,
            fillColor: polygonInfo.fillColor || '#FF0000',
            fillOpacity: parseFloat(polygonInfo.fillOpacity) || 0.35,
            map: this.map,
            editable: false, // Cambiar a true si quieres que sea editable
            draggable: false,            
        };

        const polygon = new google.maps.Polygon(polygonOptions);

        polygon.setVisible(polygonInfo.visible || false);

        let polygonManaged:PolygonManaged={
            id: polygonInfo.id, 
           // nombre: polygonInfo.label||'',
            polygon: polygon           
        }

        this.polygons.set(polygonInfo.id, polygonManaged);
        
        if(polygonInfo)
        {
            /*
            const pin = new google.maps.marker.PinElement({
                glyph: '',
                scale: 1.0,
                background: '#E53935',
                glyphColor: '#FFFFFF'
            });
            */

            console.log('Adding center marker for polygon:', polygonInfo.id);
            console.log('Center position:', polygonInfo.centro?.position);
            console.log('Center icon:', polygonInfo.centro?.icon);

            const marker = new google.maps.marker.AdvancedMarkerElement({
                map: this.map,
                position: polygonInfo.centro?.position,
                //content: pin.element,
                content: this.createCustomIcon(polygonInfo.centro?.icon|| 'default-icon.png'),
                title: polygonInfo.centro?.label,
                gmpDraggable: false,
                gmpClickable: false 
            });
        }
    }

    private createCustomIcon(iconUrl: string): HTMLElement 
    {
        //http://maps.google.com/mapfiles/ms/icons/blue-dot.png
         const markerImage = document.createElement('img');
        markerImage.src = iconUrl; // Replace with the actual path to your PNG
        //markerImage.style.width = '30px'; // Optional: Adjust size if needed
       // markerImage.style.height = '30px'; // Optional: Adjust size if needed
        return markerImage;
        
    }    

    updatePolygonManaged(polygonInfo:PolygonInfo):void
    {
        let polygon  = this.polygons.get(polygonInfo.id);

        if (polygon) 
        {
            polygon.polygon.setVisible(polygonInfo.visible || false);
        }
    }

    getPolygonManaged(id: string): PolygonManaged | undefined
    {
        return this.polygons.get(id);
    }
}

//interfaz de cara a la interfaz consumida por blazor.
//la interfaz para con blazor sigue en main.js

class MapsService 
{  
    public  version:string= 'MapsService.0.0.4';
    private mapManager : MapManager|null=null;

    constructor(element: string | HTMLElement, optionsInfo: MapOptions) 
    {
        this.mapManager = new MapManager(element, optionsInfo);
    }

    public setZoom(zoom: number): void 
    {
        if (!this.mapManager?.map) return;
        this.mapManager.map.setZoom(zoom);
    }

    public async isReady(): Promise<void> 
    {
        if (!this.mapManager) {
            throw new Error('MapManager not initialized');
        }
        
    }

    public async addMarker(markerInfo:MarkerInfo): Promise<void> 
    {
        if(!this.mapManager) return;        
        await this.mapManager.isReady();

        if (!this.mapManager?.map) return;

        let markerManager = this.mapManager?.getMarkerManaged(markerInfo.id);

        if(!markerManager)
        { 
            console.log('Adding new marker with id:', markerInfo.id);
            await this.mapManager.addMarkerManaged(markerInfo);          
        }
        else
        {
            console.log('updating marker with id:', markerInfo.id );
            await this.mapManager.updateMarkerManaged(markerInfo);
        }
    }

    public async showInfoWindow(contentInfoWindow:ContentWindowInfo): Promise<void>
    {
        if(!this.mapManager) return;        
        await this.mapManager.isReady();

        if (!this.mapManager?.map) return;

        const mark = this.mapManager.getMarkerManaged(contentInfoWindow.id);   

        if (mark && this.mapManager?.map)
        {
            const infoWindow = new google.maps.InfoWindow({                
                content: contentInfoWindow.content
            });
            infoWindow.open(this.mapManager?.map, mark.marker);
        }   
    }

    public async verMarkers(idMarker:string): Promise<MakerCoordenate | undefined>
    {
        if(!this.mapManager) return;        
        await this.mapManager.isReady();

        if (!this.mapManager?.map) return;

        let mark=this.mapManager?.getMarkerManaged(idMarker);

        if(!mark) return undefined;

        const pos = mark.marker?.position;

        if (pos )
        {
            const lat = typeof pos.lat === 'function' ? pos.lat() : pos.lat;
            const lng = typeof pos.lng === 'function' ? pos.lng() : pos.lng;
            
            return {
                id: mark?.Id ?? '',
                position: { lat: lat, lng: lng },
                address: mark?.address
            };
        }
        return undefined;
    }

    public async centerMap(coords: { lat: number; lng: number }): Promise<void> 
    {
        if(!this.mapManager) return;        
        await this.mapManager.isReady();

        if (!this.mapManager?.map) return;
        this.mapManager.map.setCenter(coords);
    }

    public async addPolygon(polygonInfo:PolygonInfo): Promise<void> 
    {
        if(!this.mapManager) return;        
        await this.mapManager.isReady();

        if (!this.mapManager?.map) return;

        let polygonManager = this.mapManager?.getPolygonManaged(polygonInfo.id);

        if(!polygonManager)
        { 
            console.log('Adding new marker with id:', polygonInfo.id);
            await this.mapManager.addPolygonManaged(polygonInfo);          
        }
        else
        {
            console.log('updating marker with id:', polygonManager.id );
            await this.mapManager.updatePolygonManaged(polygonInfo);
        }
    }
}

(window as any).MapsService = MapsService;
export default MapsService;
